/* https://developer.helpscout.com/seed/glossary/itcss/#the-triangle */
@layer settings, tools, generic, elements, objects, typography, components, overrides;
@layer settings {
  :root {
    --limegreen: hsl(163, 72%, 41%);
    --brightred: hsl(356, 69%, 56%);
    --facebook: hsl(208, 92%, 53%);
    --twitter: hsl(203, 89%, 53%);
    --instagram-start: hsl(37, 97%, 70%);
    --instagram-middle: hsl(5, 77%, 71%);
    --instagram-end: hsl(329, 70%, 58%);
    --youtube: hsl(348, 97%, 39%);
    --toggle-bg-light: hsl(230, 22%, 74%);
    --toggle-bg-start: hsl(210, 78%, 56%);
    --toggle-bg-end: hsl(146, 68%, 55%);
    --toggle-light: hsl(230, 19%, 60%);
    --toggle-button-light: hsl(228, 46%, 96%);
    --dark-bg: hsl(230, 17%, 14%);
    --dark-top-bg: hsl(232, 19%, 15%);
    --dark-card: hsl(228, 28%, 20%);
    --dark-card-hover: hsl(228, 25%, 27%);
    --dark-text1: hsl(228, 34%, 66%);
    --dark-text2: hsl(0, 0%, 100%);
    --light-bg: hsl(0, 0%, 100%);
    --light-top-bg: hsl(225, 100%, 98%);
    --light-card: hsl(227, 47%, 96%);
    --light-card-hover: hsl(228, 33%, 91%);
    --light-text1: hsl(230, 12%, 44%);
    --light-text2: hsl(230, 17%, 14%);
    --background: var(--light-bg);
    --text-color: var(--light-text2);
    --text-color2: var(--light-text1);
    --card-bg: var(--light-card);
    --card-hover: var(--light-card-hover);
    --toggle: var(--toggle-light);
    --toggle-bg: var(--toggle-bg-light);
    --toggle-button: var(--toggle-button-light);
    --font-inter: "Inter", sans-serif;
  }
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color: var(--dark-text2);
    --text-color2: var(--dark-text1);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle: var(--light-bg);
    --toggle-bg: linear-gradient(
      225deg,
      var(--toggle-bg-end) 0%,
      var(--toggle-bg-start) 98.02%
    );
    --toggle-button: var(--dark-bg);
  }
}
body.light {
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

body.dark {
  --background: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--light-bg);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-end) 0%,
    var(--toggle-bg-start) 98.02%
  );
  --toggle-button: var(--dark-bg);
}

/*
Example:

h1 {
  @include breakpoint(large) {
  font-size: 3em
  }
}

- loads breakpoint in h1 tag, 
- translates to CSS...

@media (min-width: 87.5em) {
  h1 {
    font-size: 3rem;
  }
}

*/
/**

h1 {
  font-size: rem(30)
}

results in...

h1 {
  font-size: 1.875rem
}
*/
@layer generic {
  html {
    font-size: 100%;
    box-sizing: border-box;
  }

  *, *::before, ::after {
    box-sizing: inherit;
  }

  body {
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
  }
}
@layer elements {}
@objects {}
@typography {
  :root h1,
:root h2,
:root h3 {
    margin-top: 0;
  }
  :root a,
:root a:visited,
:root a:active {
    text-decoration: none;
  }
}
@layer components {
  .card {
    padding: 1.25rem;
  }
}
@layer components {
  .navbar {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    color: white;
  }

  .navbar__title {
    font-size: 1.5rem;
    margin: 0.5rem;
  }

  .navbar__links {
    height: 100%;
  }

  .navbar__links-list {
    margin: 0;
    padding: 0;
    display: flex;
  }

  .navbar__links-item {
    list-style: none;
  }

  .navbar__link {
    text-decoration: none;
    color: white;
    padding: 1rem;
    display: block;
  }

  .navbar__link:hover {
    background-color: #555;
  }

  .navbar__toggle-button {
    position: absolute;
    top: 0.75rem;
    right: 1rem;
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 21px;
  }

  .navbar__toggle-button-bar {
    height: 3px;
    width: 100%;
    background-color: white;
    border-radius: 10px;
  }

  @media (max-width: 800px) {
    .navbar__toggle-button {
      display: flex;
    }

    .navbar__links {
      display: none;
      width: 100%;
    }

    .navbar {
      flex-direction: column;
      align-items: flex-start;
    }

    .navbar__links-list {
      width: 100%;
      flex-direction: column;
    }

    .navbar__links-list .navbar__links-item {
      text-align: center;
    }

    .navbar__link {
      padding: 0.5rem 1rem;
    }

    .navbar__links.navbar__links--active {
      display: flex;
    }
  }
}
@overrides {}
@layer settings, tools, generic, elements, objects, typography, components, overrides;